#home{
    width: 100%;
    height:100vh;
    display: flex;
    /* position: absolute; */
    text-align: center;
    align-items: center;
    justify-content: center;
    /* position: relative; */
    color:#fff;
}
#home,
button {
    border-radius: .2rem;
    text-transform: uppercase;
}
#home button:hover{
    background-color: #E085AD;
}
#homecontainer{
    /* position: absolute; */
    z-index: 2;
}
.homeheader{
    font-size: 5rem;
    margin:2px;
}
.homep{
    font-size:2.5rem;
    margin:2px;
    text-align: center;
}
a{
    color:#000;
    font-weight: bold;
    font-size: 1.25rem;
    text-decoration: none;
}
button{
    margin:1rem;
    padding:.7rem 1.5rem;
    border: none;
    background-color: #fff;
}

.contacticon{
    font-size:4rem;
}


/* PHONEFLIP*/
@media screen and (max-width:500px) and (max-height:950px){
    .homeheader{
        font-size: calc(20px + 6 * ((100vw - 320px) / 680));
    }
    .homep{
        font-size: calc(10px + 6 * ((100vw - 320px) / 680));
    }
    a{
        font-size: calc(10px + 6 * ((100vw - 320px) / 680));
    }
    button{
        padding:5px;
    }
}
/* PHONE */
@media screen and (max-width:600px) and (max-height:950px){
    .homeheader{
        font-size: calc(25px + 6 * ((100vw - 320px) / 680));
    }
    .homep{
        font-size: calc(12.5px + 6 * ((100vw - 320px) / 680));
    }
    a{
        font-size: calc(10px + 6 * ((100vw - 320px) / 680));
    }
    button{
        padding:5px;
    }
}

/* TABLETS */
@media (width < 700px){
    #home{
        margin:0 0 3rem;
    }
    .homeheader{
        font-size: calc(50px + 6 * ((100vw - 320px) / 680));
    }
    .homep{
        font-size: calc(25px + 6 * ((100vw - 320px) / 680));
    }
}

/* TABLETS */
@media screen and (min-width:900px) and ( min-height:500px){
    .homeheader{
        font-size: calc(60px + 6 * ((100vw - 320px) / 680));
    }
    .homep{
        font-size: calc(30px + 6 * ((100vw - 320px) / 680));
    }
}
